import React, { Component } from 'react';
import ConversationList from '../ConversationList';
import MessageList from '../MessageList';
//import CheckboxContainer from '../Checkbox';
import './Messenger.css';
import Modal from '../Modal/Modal.js';

const SERVER_URL = 'https://1714973e.ngrok.io'
export default class Messenger extends Component {
  constructor(props) {
    super(props);
    this.state = {
      keywords: [],
      isShowing: true,
      isFirst:true,
    };
    // console.log(this.props.appstate.state.first);

   

    this.openModalHandler = this.openModalHandler.bind(this);
    this.closeModalHandler = this.closeModalHandler.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.sendName = this.sendName.bind(this);
  }



  componentDidMount() {

   let first_time = localStorage.getItem('first_time');
   // console.log(first_time);
    if(first_time == null) {
      localStorage.setItem('first_time', 'true');
        this.setState({
            isFirst: true
        });
    }
    else{
      this.setState({
            isFirst: false
        });
    }

  }

  changePage(){
    let uuid = Math.random().toString();
      console.log(uuid);
    // change uuid for subsequent tasks
    localStorage.setItem('uuid', uuid);


    window.location.reload()
  }

  sendName(){
    let uuid = localStorage.getItem('uuid');
    fetch(SERVER_URL+"/score", {
        method: 'POST',
        body: JSON.stringify({
          uuid: uuid,
          name: this.state.nameInput,
        }),
      }).then(this.handleRedirect) ;
  }

  handleRedirect(res){
            if( res.status === 200 ){
                // redirect here
                console.log('sent');

                let uuid = localStorage.getItem('uuid');
                localStorage.setItem('uuid', Math.random().toString());

                window.location.href = 'http://convpro.com/score.html?uuid=' + uuid;
                console.log(localStorage.getItem('uuid'));
            }else {
              // Something went wrong here
              window.location.reload();
            }
          }

   handleChange(e) {
    this.setState({ nameInput: e.target.value });
  }

    openModalHandler = () => {
        this.setState({
            isShowing: true,
            isFirst: true
        });
    }

    closeModalHandler = () => {
        this.setState({
            isShowing: false,
            isFirst: false
        });
    }


  render() {
      let modelDiv;
      if(this.state.isShowing && this.state.isFirst ){
        modelDiv =  <div className = "overlay">
                    <div onClick={this.closeModalHandler} className="back-drop"></div> 
                        <Modal
                          className="modal"
                          show={this.state.isShowing}
                          close={this.closeModalHandler}>
                          <div style={{display: 'inline' }}>
Not many people have what it takes to be a <strong>MASTER OF CONVERSATION.</strong> Anybody can <em>write a response</em> but to <em>put yourself in someone's shoes and predict their response</em> is a challenge indeed.
<br/>
<br/>
On the left, you'll see an on-going conversation. Your mission, should you choose to accept, is to <strong>predict what was said next</strong>. You may guess as many times as you'd like. 
<br/>
<br/>
We will score your guesses based on how close you were to the <strong>real response</strong>. Remember, punctuation and grammar matters. Try to match the person you're responding as!
<br/>
<br/>
Good luck! Only the best shall succeed.
                          </div>
                            </Modal>
                    </div>
      }
      else if(this.state.isShowing) {
         modelDiv =  <div className = "overlay">
                    <div onClick={this.closeModalHandler} className="back-drop"></div> 
                        <Modal
                          className="modal"
                          show={this.state.isShowing}
                          close={this.closeModalHandler}>
                          <div style={{display: 'flex', justifyContent: 'center',   flexDirection: 'column', alignItems: 'center'}}>
                          <div className="scoreinfo">Score for last round: {this.state.previous_score} </div><br />
                          <div className="scoreinfo">Total score: {this.state.total_score} </div><br />
                          <div className="scoreinfo">Round: {this.state.turn} out of 5</div>
                          </div>
                           </Modal>
                           </div>
      }
      else if(this.state.conversation_done){
          modelDiv =  <div className = "overlay">
                    <div onClick={this.closeModalHandler} className="back-drop"></div> 
                        <Modal
                          className="modal"
                          show={true}
                          close={this.closeModalHandler}>
        <div style={{display: 'flex', justifyContent: 'center',   flexDirection: 'column', alignItems: 'center'}}>
        All rounds completed: &nbsp;&nbsp;
        <button className="btninstrc" onClick={this.changePage}>Play again</button>
        <br />
        Please enter your name here and press Submit Score to finish
        <br />
                <input rows="4" id ="composearea"
                type="text"
                className="compose-name"
                placeholder="Type your name here"
                onChange={this.handleChange}
              />
        <br />      
        <button className="btninstrc" onClick={this.sendName}>Submit Score</button>
        </div>
                           </Modal>
                           </div>
      } 


      return (
      <div className="messenger">

        {modelDiv}     

        {/* <Toolbar
          title="Messenger"
          leftItems={[
            <ToolbarButton key="cog" icon="ion-ios-cog" />
          ]}
          rightItems={[
            <ToolbarButton key="add" icon="ion-ios-add-circle-outline" />
          ]}
        /> */}

        {/* <Toolbar
          title="Conversation Title"
          rightItems={[
            <ToolbarButton key="info" icon="ion-ios-information-circle-outline" />,
            <ToolbarButton key="video" icon="ion-ios-videocam" />,
            <ToolbarButton key="phone" icon="ion-ios-call" />
          ]}
        /> */}

        <div className="scrollable content">
          <MessageList keywords={this.state.keywords} messengerobj ={this}/>
        </div>

      </div>

    );
        
  }
}
