import React, { Component } from 'react';
import Compose from '../Compose';
import Toolbar from '../Toolbar';
import ToolbarButton from '../ToolbarButton';
import Message from '../Message';
import moment from 'moment';
import ReactTooltip from 'react-tooltip'


import './MessageList.css';

const SERVER_URL = 'https://1714973e.ngrok.io'
//"https://a1b64b58.ngrok.io
export default class MessageList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      messages: [],
      render: true,
      ranges: [],
      all_responses : []
    };
    this.sendDataToServer = this.sendDataToServer.bind(this);
    this.addMessage = this.addMessage.bind(this);
  }




  sendDataToServer(text_entered){
    console.log('got back ' + text_entered);

    let keyword_highlights = [];
    let ranges = this.state.ranges;
    for(let i = 0; i < ranges.length; i++) {
      console.log(ranges[i]);
      console.log(ranges[i].ranges);
      for(let j = 0; j < ranges[i].ranges.length; j++) {
        keyword_highlights.push({ind: ranges[i].id, 
                                 text: ranges[i].text.substring(ranges[i].ranges[j].start,
                                                                ranges[i].ranges[j].end),
                                 start: ranges[i].ranges[j].start,
                                 end: ranges[i].ranges[j].end});
      }
    }
    fetch(SERVER_URL+"/data", {
        method: 'POST',
        body: JSON.stringify({
          uuid: localStorage.getItem('uuid'),
          response: text_entered,
          all_responses: this.state.all_responses, 
          fold : this.state.fold,
          dialogue_utterance_id: this.state.dialogue_utterance_id,
          keywords : this.props.keywords,
          keyword_highlights: keyword_highlights,
        }),
      });

    // hacky way to add delay after submitting
    this.setState(prevState => {
      return {
        ...prevState,
        render: false
        };
      });
       //to be used for delayed display of next screen
       window.location.reload();
    /*
    setTimeout(function() { //Start the timer
      this.setState(prevState => {
      return {
        ...prevState,
        render: true
        };
      }); //After 1 second, set render to true
      window.location.reload();
    }.bind(this), 500);
    */ 

    
  }

  convertToTuple(json_conversation){
    // console.log(json_conversation);

    if ('conversation_done' in json_conversation){

        localStorage.removeItem('first_time');
        // save conversation_done to state
        this.setState(prevState => {
          return {
            ...prevState,
            conversation_done: true,
            total_score: json_conversation.total_score, 
            previous_score : json_conversation.previous_score,
            turn : json_conversation.turn,
          };
        });

      this.props.messengerobj.setState(prevState => {
          return {
            ...prevState,
          conversation_done: true,
          isShowing : false,
          uuid: localStorage.getItem('uuid'),
          };
        });


        return [];
        // reset uuid
    }
    const dialogue_utterance_id = json_conversation.dialogue_utterance_id;
    const dialogue = json_conversation.dialogue;
    const fold = json_conversation.fold;
    const tuplelist = [];
    const authors = ['A', 'B'];
    const previous_score = json_conversation.previous_score;
    const total_score = json_conversation.total_score;
    const turn = json_conversation.turn;
    console.log(total_score + " " + previous_score);
    this.setState(prevState => {
      return {
        ...prevState,
        fold: fold,
        dialogue_utterance_id: dialogue_utterance_id,
        total_score: total_score,
        previous_score : previous_score,
        turn : turn,
      };
    });

    this.props.messengerobj.setState(prevState => {
      return {
        ...prevState,
        fold: fold,
        dialogue_utterance_id: dialogue_utterance_id,
        total_score: total_score,
        previous_score : previous_score,
        turn : turn,
      };
    });
    
    let author_index = 0;
    for (let i = 0; i< dialogue.length; i++){
        let dialogue_text = dialogue[i].text;
        tuplelist[i] = {id: i, author: authors[author_index], message: dialogue_text, timestamp: new Date().getTime()};
        author_index = 1 - author_index;
    }


    return tuplelist;
  }

  componentDidUpdate() {
    this.messagesEnd.scrollIntoView({ behavior: "smooth" });
  }

  componentDidMount() {
    this.messagesEnd.scrollIntoView({ behavior: "smooth" });

    let uuid = localStorage.getItem('uuid');
    if(uuid == null) {
      uuid = Math.random().toString();
      localStorage.setItem('uuid', uuid);
    }
    fetch(SERVER_URL+"/data?uuid="+uuid)
      .then(response => response.json())
      .then(data => this.getMessages(this.convertToTuple(data)));
//      .then(data => this.getMessages(data.map((row, i) => ({id: i, author: row[0], message: row[1], timestamp: new Date().getTime()}))));
    

  }

  addMessage(text_entered){
    // console.log('new response ' + text_entered);
    let oldmessages = this.state.messages;
    let oldauthor = this.state.author;
    // console.log(oldmessages);
    // console.log(oldauthor);
    let newmessage = {id: oldmessages.length+1, author: oldauthor, message: text_entered, timestamp: new Date().getTime()};
    this.setState(prevState => {
      return {
        ...prevState,
        all_responses: this.state.all_responses.concat([newmessage])
      };
    });
  }

  getMessages = (messages) => {
    let authorID = '';
    if (messages.length>0){
        authorID = messages[messages.length-1]['author'] == 'A' ? 'B' : 'A';
    }
    else {authorID = 'NO MESSAGE';}

    this.setState(prevState => {
      return {
        ...prevState,
        messages: messages,
        author: authorID,
      };
    });
    // console.log(this.state);
  }

  changePage(){
    window.location.reload()
  }

  renderReplies() {
    let i = 0;
    let messageCount = this.state.all_responses.length;
    let messages = [];

    if ('conversation_done' in this.state){
      if (this.state.conversation_done == true){
        return <div>Task completed. Thank you.</div>
        console.log(this.state);
      }
    }

    if(this.state.render == false) { //If this.state.render == true, which is set to true by the timer.
        return <div>Response submitted, Click for next conversation.
                  <button className="btninstrc"   onClick={this.changePage}>Go to next conversation</button>
              </div> //Add dom elements
        
    }

    while (i < messageCount) {
      let previous = this.state.all_responses[i - 1];
      let current = this.state.all_responses[i];
      let next = this.state.all_responses[i + 1];
      // console.log(this.state.author);
      let isMine = current.author === this.state.author;
      let currentMoment = moment(current.timestamp);
      let prevBySameAuthor = false;
      let nextBySameAuthor = false;
      let startsSequence = true;
      let endsSequence = true;
      let showTimestamp = false;

      if (previous) {
        let previousMoment = moment(previous.timestamp);
        let previousDuration = moment.duration(currentMoment.diff(previousMoment));
        prevBySameAuthor = previous.author === current.author;
        
        if (prevBySameAuthor && previousDuration.as('hours') < 1) {
          startsSequence = false;
        }

        if (previousDuration.as('hours') < 1) {
          showTimestamp = false;
        }
      }

      if (next) {
        let nextMoment = moment(next.timestamp);
        let nextDuration = moment.duration(nextMoment.diff(currentMoment));
        nextBySameAuthor = next.author === current.author;

        if (nextBySameAuthor && nextDuration.as('hours') < 1) {
          endsSequence = false;
        }
      }

      messages.push(
        <Message
          key={i}
          id={i}
          isMine={isMine}
          startsSequence={startsSequence}
          endsSequence={endsSequence}
          showTimestamp={showTimestamp}
          data={current}
          prnt={this}
        />
      );

      // Proceed to the next message.
      i += 1;
    }

    this.messages = messages;
    return messages;
  }

  renderMessages() {
    let i = 0;
    let messageCount = this.state.messages.length;
    let messages = [];

    if ('conversation_done' in this.state){
      if (this.state.conversation_done == true){
        return <div>Task completed. Thank you.</div>
        console.log(this.state);
      }
    }

    if(this.state.render == false) { //If this.state.render == true, which is set to true by the timer.
        return <div>Response submitted, Click for next conversation.
                  <button className="btninstrc"   onClick={this.changePage}>Go to next conversation</button>
              </div> //Add dom elements
        
    }

    while (i < messageCount) {
      let previous = this.state.messages[i - 1];
      let current = this.state.messages[i];
      let next = this.state.messages[i + 1];
      // console.log(this.state.author);
      let isMine = current.author === this.state.author;
      let currentMoment = moment(current.timestamp);
      let prevBySameAuthor = false;
      let nextBySameAuthor = false;
      let startsSequence = true;
      let endsSequence = true;
      let showTimestamp = true;

      if (previous) {
        let previousMoment = moment(previous.timestamp);
        let previousDuration = moment.duration(currentMoment.diff(previousMoment));
        prevBySameAuthor = previous.author === current.author;
        
        if (prevBySameAuthor && previousDuration.as('hours') < 1) {
          startsSequence = false;
        }

        if (previousDuration.as('hours') < 1) {
          showTimestamp = false;
        }
      }

      if (next) {
        let nextMoment = moment(next.timestamp);
        let nextDuration = moment.duration(nextMoment.diff(currentMoment));
        nextBySameAuthor = next.author === current.author;

        if (nextBySameAuthor && nextDuration.as('hours') < 1) {
          endsSequence = false;
        }
      }

      messages.push(
        <Message
          key={i}
          id={i}
          isMine={isMine}
          startsSequence={startsSequence}
          endsSequence={endsSequence}
          showTimestamp={showTimestamp}
          data={current}
          prnt={this}
        />
      );

      // Proceed to the next message.
      i += 1;
    }

    this.messages = messages;
    return messages;
  }

  render() {
    return(
      <div className="all">
        <Toolbar
          title="ConvPro"
          rightItems={[
            (<div key="info"className="score-all">
        <div className="score" style={{textAlign:'center'}}>{"TOTAL SCORE: "+ this.state.total_score}</div>
        <div className="score">{"ROUND: "+ (this.state.turn+1) + " out of 5" }</div>
          </div>),
          ]}
        />

        <div className="message-list">
          <div className="left-container">
            <div className="message-list-container">{this.renderMessages()}</div>
            <div style={{ float:"left", clear: "both" }}
                 ref={(el) => { this.messagesEnd = el; }}>
            </div>
          </div>
          <div className="input-container" style={{height: 'calc(100% - 60px)'}}>
            <h1>Guess the response!</h1>
                          <div style={{display: 'inline', textAlign: 'left'}}>
Not many people have what it takes to be a <strong>MASTER OF CONVERSATION.</strong> Anybody can simply <em>write a response</em> but to <em>put yourself in someone's shoes and predict their response</em> is a challenge indeed.
<br/>
<br/>
On the left, you'll see an on-going conversation. Your mission, should you choose to accept, is to <strong>predict what was said next</strong>. You may guess as many times as you'd like.
<br/>
<br/>
We will score your guesses based on how close you were to the <strong>real response</strong>. Remember, punctuation and grammar matters. Try to match the person you're responding as!
<br/>
<br/>
Good luck! Only the best shall succeed.
                          </div>

            <div className="message-list-container">{this.renderReplies()}</div>
            <Compose enterHandler={this.sendDataToServer} messageAddHandler = {this.addMessage}  rightItems={[
            ]}/>

          </div>
        </div>


      </div>
    );
  }
}
