import React, { Component } from 'react';
import './Compose.css';

export default class Compose extends Component {


  constructor(props) {
    super(props);

    this.state = {
      textValue : '',
      textInputValue : 'Type a response and press enter',
      responseRecorded: false
    }


    this.handleChangeTextInput = this.handleChangeTextInput.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
    this.handleButtonClick = this.handleButtonClick.bind(this);
    this.handleAddMessage = this.handleAddMessage.bind(this);
  }

  handleChangeTextInput(event) {
    // console.log(event.target.value.length);
    this.setState({textValue: event.target.value});
  }

  handleAddMessage(event){
    event.preventDefault(); 
    // console.log(this.state.textValue);
    if(this.state.textValue.length <5){
        alert('Plese enter longer response');
        // return;
      }
      else{
        this.setState({responseRecorded: true });
        this.props.messageAddHandler(this.state.textValue);
        document.getElementById("composearea").value = "";
        document.getElementById("composearea").placeholder = this.state.textInputValue;
      }
  }

  handleButtonClick(event) {
    this.props.enterHandler(this.state.textValue);
  }

  handleKeyPress = (event) => {
    if(event.key === 'Enter'){
      event.preventDefault();
      
      if(event.target.value.length <5){
        alert('Plese enter longer response');
        // return;
      }
      else{
        this.setState({responseRecorded: true });
        this.props.messageAddHandler(event.target.value);
        // event.preventDefault();
        // document.getElementById("composearea").disabled = true;
        document.getElementById("composearea").value = "";
        document.getElementById("composearea").placeholder = this.state.textInputValue;
      }
    }
  }




  render() {
    return (
      <div className="compose" id ='composediv'>
        <textarea rows="4" id ="composearea"
          type="text"
          className="compose-input"
          placeholder={this.state.textInputValue}
          onKeyPress={this.handleKeyPress}
          onChange={this.handleChangeTextInput}
        ></textarea>
      {this.state.responseRecorded &&
          <button className="btninstrc" onClick={this.handleButtonClick}>Submit Round</button>
      }
      
        {

          this.props.rightItems
        }
      </div>
    );
  }
}
